<template>
  <div class="redisVersion">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" size="small" type="warning" @click="handleAdd()">{{$t('common.add')}}</el-button>
    </div>
    <el-form label-width="70px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="版本号：">
            <el-input type="text" v-model.trim="seacrchContent"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" size="small" @click="redisVerFind" icon="el-icon-search">
            {{$t('common.search')}}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table stripe :data="redisVersion" v-loading="loading" style="width: 100%">
      <el-table-column align="center" prop="name" :label="$t('redis.version.versionName')" width="150">
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.version.version')" width="110" prop="versionNo">
      </el-table-column>
      <el-table-column align="center" prop="path" :label="$t('redis.version.savePath')">
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.version.versionDesc')" width="110" prop="remark">
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.version.creater')" width="110" prop="creator">
      </el-table-column>
      <el-table-column align="center" :label="$t('redis.version.creationDate')" width="160" prop="createTime">
      </el-table-column>
      <el-table-column :label="$t('common.operation')" width="190">
        <template slot-scope="scope">
          <el-button type="text" size="mini" icon="el-icon-edit" @click="handleEdit(scope.row)">
            {{$t('common.edit')}}
          </el-button>
          <el-button type="text" size="mini" icon="el-icon-download" @click="download(scope.row)">
            {{$t('common.download')}}
          </el-button>
          <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete" @click="deleteRow(scope.row)">{{$t('common.delete')}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination background @current-change="handleCurrentChange" :current-page="pageNo" :page-size="limit" layout="total, prev, pager, next, jumper" :total="totalPage">
      </el-pagination>
    </div>
    <!-- 添加弹出框 -->
    <el-dialog :title="way" :visible.sync="addVisible" width="40%" @close="close" center>
      <el-form ref="verAddForm" :model="verAddForm" :rules="inputRules" status-icon label-width="100px" size="small">
        <el-row :gutter="10">
          <el-col :span="24">
            <el-form-item label="软件包来源">
              <el-radio-group v-model="verAddForm.method" @change="radioChange">
                <el-radio label="Local">{{$t('common.local')}}</el-radio>
                <el-radio label="Remote">{{$t('common.server')}}</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10" v-show="verAddForm.method === 'Local'">
          <el-col :span="19">
            <el-form-item class="is-required" ref="inputFileLocal" prop="localPath" :label="$t('coreApp.tableVersion.path')">
              <el-input v-model="radio" placeholder="请选择路径"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <input type="file" class="input-file" :value="fileValue" @change="handleChange($event)" accept=".zip, .tar, .gz">
            <el-button type="primary" size="small">{{$t('common.viewFile')}}</el-button>
          </el-col>
        </el-row>
        <el-row :gutter="10" v-show="verAddForm.method === 'Remote'">
          <el-col :span="23">
            <el-form-item class="is-required" ref="inputFileRemote" prop="remotePath" :label="$t('coreApp.tableVersion.path')">
              <el-input v-model.trim="verAddForm.path" placeholder="请输入路径(例 ./software/Redis/2.8.19/redis-2.8.19.tar)"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="name">
              <el-input v-model.trim="verAddForm.name" type="text" maxlength="50" placeholder="请输入1-50位字符"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.versionNum')" prop="versionNo">
              <el-input v-model.trim="verAddForm.versionNo" type="text" maxlength="20" placeholder="请输入1-20位字符"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="23">
            <el-form-item :label="$t('coreApp.tableVersion.desc')">
              <el-input v-model.trim="verAddForm.remark" type="textarea" maxlength="100" placeholder="请输入1-100位字符"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10" style="padding-top:9px">
          <el-col :span="23">
            <el-form-item>
              <el-progress :text-inside="true" :stroke-width="18" :percentage="percentageNum" status="success"></el-progress>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveConfirm" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="close" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
    <!-- edit弹出框 -->
    <el-dialog title="修改redis版本" :visible.sync="editVisible" width="40%" center>
      <el-form ref="verEditForm" :model="verEditForm" status-icon label-width="100px" size="small">
        <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.name"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.versionNum')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.versionNo" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.type')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input :value="verEditForm.fileType === 'FULL' ? '全量' : '增量'" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.createTime')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.createTime" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.createPerson')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.creator" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.path')" prop="creator">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.path" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.desc')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="verEditForm.remark" type="textarea"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveConfirm" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="editVisible = false" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      percentageNum: 0,
      pageNo: 1, // 当前页数
      limit: 5, // 每页条数
      totalPage: 10, // 总页数,
      loading: true,
      seacrchContent: '',
      // 查询规则
      inputRules: {
        path: [
          { required: true, message: '请输入', trigger: 'blur' }
        ],
        radio: [
          { required: true, message: '', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入版本名称', trigger: 'blur' }
        ],
        versionNo: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ],
        creator: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ]
      },
      way: '', // 弹出框名字
      addVisible: false, // 添加器
      editVisible: false,
      // 添加模板
      verAddForm: {
        method: 'Local',
        moType: 'Redis',
        fileType: 'FULL',
        creator: 'admin'
      },
      // 编辑模板
      verEditForm: {},
      radio: '',
      fileValue: ''
    }
  },
  computed: {
    ...mapState({
      redisVersion: state => state.redis.redisVersion
    })
  },
  mounted() {
    this.redisVerFind()
  },
  methods: {
    ...mapActions('redis', ['findRedisVersion', 'redisVersionAdd', 'redisVersionUpdate', 'redisVersionRemove', 'redisVersionDownload']),
    // 翻页事件
    handleCurrentChange(val) {
      this.pageNo = val
      this.redisVerFind()
    },
    // 查询
    redisVerFind() {
      const vm = this
      this.loading = true
      vm.findRedisVersion(vm)
    },
    // 添加
    handleAdd() {
      this.way = '添加redis版本'
      this.verAddForm = {
        method: 'Local',
        moType: 'Redis',
        fileType: 'FULL',
        creator: window.localStorage.getItem('userName')
      }
      this.percentageNum = 0
      this.radio = ''
      this.fileValue = ''
      this.addVisible = true
    },
    close() {
      this.addVisible = false
      this.$refs['verAddForm'].clearValidate()
      this.fileValue = '' // 重置input file值
    },
    // 编辑一行
    handleEdit(row) {
      this.way = '修改redis版本'
      this.editVisible = true
      this.verEditForm = { ...row }
    },
    // 添加/编辑-确定
    saveConfirm() {
      const vm = this
      if (vm.way === '添加redis版本') {
        vm.$refs['verAddForm'].validate(valid => {
          if (valid) {
            if (!vm.radio && vm.verAddForm.method === 'Local') {
              vm.$message({
                type: 'warning',
                message: '请上传软件包'
              })
              return
            }
            if (!vm.verAddForm.path && vm.verAddForm.method === 'Remote') {
              vm.$message({
                type: 'warning',
                message: '请输入软件包路径'
              })
              return
            }
            vm.redisVersionAdd(vm)
          }
        })
      }
      if (vm.way === '修改redis版本') {
        vm.redisVersionUpdate(vm)
        this.editVisible = false
      }
    },
    // 删除一行
    deleteRow(row) {
      const vm = this
      vm.verEditForm.id = row.id
      vm.conmmonMethods.makePopBox(vm, '删除').then(() => {
        vm.redisVersionRemove(vm)
      })
    },
    handleChange(e) {
      let box = ['.zip', '.gz', '.tar']
      let suffixName = this.commonFunc.getSuffixName(e.target.files[0].name)
      if (box.includes(suffixName)) {
        this.verAddForm.sourceFile = e.target.files[0]
        this.radio = e.target.value
      } else {
        this.$message({
          type: 'warning',
          message: '文件类型不正确,请重新选择文件类型(.tar / .tar.gz / .zip)'
        })
      }
    },
    radioChange(val) {
      this.verAddForm.method = val
      this.$nextTick(() => {
        if (this.$refs.inputFileRemote) {
          this.$refs.inputFileRemote.resetField()
        }
        if (this.$refs.inputFileLocal) {
          this.$refs.inputFileLocal.resetField()
        }
        this.fileValue = ''
        this.radio = ''
        this.verAddForm.sourceFile = ''
        this.verAddForm.path = ''
      })
    },
    download(row) {
      const vm = this
      vm.conmmonMethods.makePopBox(vm, '下载').then(() => {
        vm.downPath = row.path
        vm.redisVersionDownload(vm)
      })
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
.input-file {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
</style>



